import React from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { updateTickets } from '../services/members_table';
import { addReturnedHistory } from '../services/returned_history_table';
import Button from './Button';
import Navigation from './Navigation';
import styled from 'styled-components';

const homeUrl = process.env.PUBLIC_URL;

const SLink = styled.div`
  font-weight: bold;
`;

const ReturnTickets = () => {
  const location = useLocation();
  const member = location.state;
  const navigate = useNavigate();

  const cancel = () => {
    navigate(`${homeUrl}/member/${member.id}`);
  };

  const reset = async () => {
    await updateTickets({ id: member.id, tickets: 0 });

    const today = new Date(Date.now());
    const refund = (member.tickets - 2) * member.ticket_price;
    await addReturnedHistory({
      member_id: member.id,
      return_date: today,
      tickets: member.tickets,
      refund,
    });

    navigate(`${homeUrl}/member/${member.id}`);
  };

  return (
    <>
      <Navigation title="" />
      <hr />
      <SLink>
        <Link to={`${homeUrl}`}></Link>
      </SLink>
      <hr />
      <div>{member.name}</div>
      <div>{member.tickets}</div>
      <div>
        {member.tickets - 2}
        {member.tickets * member.ticket_price - member.ticket_price * 2}
      </div>
      <div></div>
      <Button onClick={reset}></Button>
      <Button onClick={cancel}></Button>
    </>
  );
};

export default ReturnTickets;
